<template>
  <cube-popup type="extend-popup" ref="popup" class="extend-popup" :maskClosable="maskClosable">
    <div class="extend-popup-content" :class="type === 'correct' ? 'correct': ''">
      <span class="icon"></span>{{content}}
    </div>
 </cube-popup>
</template>
<script>

export default {
  data() {
    return {
      content: '',
      type: '',
      maskClosable: false,
      timer: '',
    };
  },
  name: 'extend-popup',
  methods: {
    show({
      content, type, time, maskClosable,
    }) {
      this.content = content;
      this.type = type;
      this.maskClosable = maskClosable;
      this.$refs.popup.show();
      this.timer && clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.$refs.popup.hide();
      }, time || 3000);
    },
  },
};
</script>
<style lang="stylus" scoped>
  @import "../assets/styles/common.styl";
  .extend-popup {
    .extend-popup-content {
      padding: 20px;
      background:$color-white;
      border-radius:4px;
      color: $color-dark-gray;
      display: flex;
      align-items: center;
      font-size: $size-middle;
      text-align: left;
      &.correct {
        color:$dark-blue;
        .icon {
          background-image: url('assets/images/ok.png');
        }
      }
      .icon {
        width: 30px;
        height: 30px;
        display: inline-block;
        background-size: 100%;
        background-repeat: no-repeat;
        flex-shrink: 0;
        margin-right: 8px;
        background-image: url('assets/images/err.png');
      }
    }
  }
</style>
