<template>
  <div class="bg">
    <div class="invite-wrap">
      <div class="invite-title"></div>
      <div class="invite-body">

        <div class="invite-content">
          <!-- 邀请者+组织机构 -->
          <div class="inviter word-break">
             {{detail.organization}}&nbsp;{{detail.invitation.people_name}}<br/>{{$t("common.inviteYouToVisit")}}
          </div>
          <!-- 事由 -->
          <div class="invite-item">
            {{$t("common.visitReason")}}<br/>
            /<br/>
            {{detail.people.reasons_for_access}}
          </div>
          <!-- 时间 -->
          <div class="invite-item">
            {{$t("common.visitTime")}}<br/>
            /<br/>
            {{detail.people.start_time | dateformat('YYYY.MM.DD HH:mm')}}<br/>
            ~{{detail.people.end_time | dateformat('YYYY.MM.DD HH:mm')}}
          </div>
        </div>

      </div>
    </div>
    <div class="form-wrap">
      <div class="comment">{{$t("common.invitationPhotoComment")}}</div>
      <div>
          <u-upload
            class="white"
            v-model="model.images"
            @fileSubmitted="fileSubmitted"
          @filesAdded="filesAdded" />
        </div>
        <button type="submit" @click="submitHandler" class="button">{{$t("common.confirm")}}</button>
    </div>

    <extend-popup ref="extendPopup"></extend-popup>
  </div>
</template>

<script>
import UUpload from '@/components/U-Upload.vue';
import ExtendPopup from '@/components/ExtendPopup.vue';

const apis = {
  invitation: '/h5/invitation',
  accept: '/h5/invitation/accept',
};
export default {
  data() {
    return {
      detail: {
        organization: '',
        invitation: {},
        people: {},
      },
      model: {
        images: [],
      },
      uploading: false,
      invitationId: '',
    };
  },
  components: {
    UUpload,
    ExtendPopup,
  },
  mounted() {
    let { invitationId } = this.$route.params;
    // const { lang } = this.$route.params;
    // this.$i18n.locale = lang;
    // localStorage.setItem('lang', lang);
    invitationId = invitationId - 0 || '';
    this.invitationId = invitationId;
    if (!invitationId) {
      return;
    }
    this.http.get(apis.invitation, {
      params: {
        invitation_id: invitationId,
      },
    }).then((res) => {
      if (res.code === 1000) {
        this.detail = res.data;
      } else {
        console.log(this.$t('common.failedToGetInvitationInfo'));
      }
    }).catch(() => {
      console.log(this.$t('common.failedToGetInvitationInfo'));
    });
  },
  methods: {
    submitHandler() {
      const { images } = this.model;
      const image = images[0] && images[0].base64;
      if (!image) {
        this.showToastType(this.$t('common.photoRequired'));
      } else if (this.invitationId) {
        const formData = {
          image,
          invitation_id: this.invitationId,
        };
        this.http.post(apis.accept, formData).then((res) => {
          if (res.code === 1000) {
            this.showToastType(this.$t('common.alreadyAcceptInvitation'), 'correct', 5000);
          } else {
            this.showToastType(res.msg ? `${res.msg}${res.reason ? `:${res.reason}` : ''}` : this.$t('common.failedToConfirmInvitation'));
          }
        }).catch(() => {
          this.showToastType(this.$t('common.failedToConfirmInvitation'));
        });
      }
    },
    filesAdded() {
      this.uploading = true;
    },
    fileSubmitted() {
      this.uploading = false;
    },
    showToastType(txt, type = 'error', time) {
      this.$refs.extendPopup.show({
        content: txt,
        time: time || 3000,
        type,
        maskClosable: type === 'error',
      });
    },
  },
  computed: {
    locale() {
      return this.$i18n.locale;
    },
    isEn() {
      return this.locale === 'en-US';
    },
  },
};
</script>

